import React, { useState, useRef, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';

interface ChatMessage {
  id: string;
  type: 'user' | 'ai' | 'system';
  content: string;
  timestamp: Date;
  vectorContext?: string[];
  confidence?: number;
  relatedTopics?: string[];
}

interface VectorContext {
  topic: string;
  relevance: number;
  source: string;
  snippet: string;
}

interface VectorLLMChatProps {
  userRole: 'student' | 'teacher';
  studentId?: string;
  subjectId?: string;
}

export const VectorLLMChat: React.FC<VectorLLMChatProps> = ({ 
  userRole, 
  studentId, 
  subjectId 
}) => {
  const { user } = useAuth();
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [inputMessage, setInputMessage] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [vectorContexts, setVectorContexts] = useState<VectorContext[]>([]);
  const [showVectorInfo, setShowVectorInfo] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Initialize with welcome message
  useEffect(() => {
    const welcomeMessage: ChatMessage = {
      id: 'welcome',
      type: 'ai',
      content: userRole === 'student' 
        ? `Ahoj ${user?.name}!  Jsem Professor Kwark, tvj AI tutor! Mm pstup ke vem tvm studijnm materilm a pokroku. Na co se chce zeptat?`
        : `Vtejte ${user?.name}!  Jsem v AI asistent s pstupem ke kompletn vektorov databzi vech materil a studentskch interakc. Jak vm mohu pomoci?`,
      timestamp: new Date(),
      confidence: 1.0
    };
    setMessages([welcomeMessage]);
  }, [user?.name, userRole]);

  // Auto scroll to bottom
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  // Simulate vector search and LLM response
  const generateAIResponse = async (userMessage: string): Promise<ChatMessage> => {
    // Simulate vector database search
    const mockVectorContexts: VectorContext[] = [
      {
        topic: 'Newtonovy zkony',
        relevance: 0.95,
        source: 'Uebnice fyziky 8. tda',
        snippet: 'Prvn Newtonv zkon k, e tleso setrvv v klidu nebo rovnomrnm pohybu...'
      },
      {
        topic: 'Gravitan sla',
        relevance: 0.87,
        source: 'Studentova aktivita',
        snippet: 'Student ml pote s vpotem gravitan sly na planet Mars...'
      },
      {
        topic: 'Sla a zrychlen',
        relevance: 0.82,
        source: 'Kvz z minulho tdne',
        snippet: 'Sprvn odpovdl na 7 z 10 otzek o vztahu sly a zrychlen...'
      }
    ];

    setVectorContexts(mockVectorContexts);

    // Simulate AI response generation
    const aiResponses = {
      student: [
        `Na zklad tvch pedchozch aktivit vidm, e si dobe rozum se zklady!  K ${userMessage.toLowerCase()} ti mohu ct: \n\nZ naich materil o Newtonovch zkonech vyplv, e je dleit si zapamatovat vztah F = m  a. Vidm, e v minulm kvzu jsi ml 85% spnost! \n\n Tip: Zkus si procviit pklady s rznmi planetami - gravitace na Marsu je jin ne na Zemi!`,
        `Skvl otzka!  Podle vektorovch dat z tvho profilu vidm, e m talent na logick mylen. \n\nK tmatu ${userMessage.toLowerCase()}: Pamatuje si n experiment s kyvadlem? Pesn tady se aplikuj principy, kter se pt. \n\n Chce si to procviit v naem fyziklnm simultoru?`,
        `Vidm z tv historie, e t zajmaj praktick aplikace!  \n\nK ${userMessage.toLowerCase()} - tohle je perfektn pklad toho, jak fyzika funguje v relnm svt. Vzpomn si na nai diskusi o autech a brzdn? \n\n Tvoje porozumn tto oblasti je na 78% - mme prostor pro zlepen!`
      ],
      teacher: [
        `Podle analzy vektorovch dat z va tdy k tmatu ${userMessage.toLowerCase()}: \n\n **Statistiky tdy:**\n 67% student m problmy s tmto konceptem\n Doporuuji vce praktickch pklad\n Anna Novkov vynik (95%), Tom Svoboda potebuje pomoc (45%)\n\n **AI doporuen:** Pouijte vizuln demonstrace a skupinovou prci.`,
        `Na zklad dokument kter jste nahrli a studentskch interakc: \n\n **Materilov pokryt:** Tma ${userMessage.toLowerCase()} je dobe podloeno v kapitole 3.2\n\n **Studentsk data:** Prmrn doba strven na tomto tmatu: 23 minut\n\n **Rizikov faktory:** 3 studenti vykazuj znaky nepochopen zklad`,
        `Vektorov analza ukazuje souvislosti s pedchozmi tmaty: \n\n **Propojen:** ${userMessage.toLowerCase()} navazuje na gravitaci (korelace 0.89)\n\n **Predikce:** Studenti kte pochop toto tma maj 92% anci na spch v dalch kapitolch\n\n **Doporuen aktivity:** Simulace, een pklad, diskusn frum`
      ]
    };

    const responses = aiResponses[userRole];
    const randomResponse = responses[Math.floor(Math.random() * responses.length)];

    return {
      id: `ai-${Date.now()}`,
      type: 'ai',
      content: randomResponse,
      timestamp: new Date(),
      vectorContext: mockVectorContexts.map(ctx => `${ctx.topic} (${Math.round(ctx.relevance * 100)}%)`),
      confidence: 0.92,
      relatedTopics: ['Sla', 'Zrychlen', 'Gravitace', 'Energie']
    };
  };

  const sendMessage = async () => {
    if (!inputMessage.trim()) return;

    const userMessage: ChatMessage = {
      id: `user-${Date.now()}`,
      type: 'user',
      content: inputMessage,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsLoading(true);

    try {
      // Simulate network delay
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      const aiResponse = await generateAIResponse(inputMessage);
      setMessages(prev => [...prev, aiResponse]);
    } catch (error) {
      const errorMessage: ChatMessage = {
        id: `error-${Date.now()}`,
        type: 'system',
        content: 'Omlouvm se, dolo k chyb. Zkuste to prosm znovu.',
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsLoading(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const getMessageIcon = (type: string) => {
    switch (type) {
      case 'user': return userRole === 'student' ? '' : '';
      case 'ai': return '';
      case 'system': return '';
      default: return '';
    }
  };

  return (
    <div className="flex flex-col h-full">
      {/* Chat Header */}
      <div className="glass-header p-4 mb-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <div className="text-2xl"></div>
            <div>
              <h3 className="font-semibold text-gray-900 text-glass">
                Professor Kwark - AI Tutor
              </h3>
              <p className="text-sm text-gray-600 text-glass">
                {userRole === 'student' ? 'Tvj osobn AI uitel' : 'AI asistent pro uitele'}
              </p>
            </div>
          </div>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setShowVectorInfo(!showVectorInfo)}
              className={`glass-button px-3 py-1 text-sm ${showVectorInfo ? 'bg-blue-100 text-blue-700' : ''}`}
            >
               Vector Info
            </button>
            <div className="flex items-center space-x-1">
              <div className="w-2 h-2 bg-green-500 rounded-full"></div>
              <span className="text-xs text-gray-600 text-glass">Online</span>
            </div>
          </div>
        </div>
      </div>

      {/* Vector Context Info */}
      {showVectorInfo && vectorContexts.length > 0 && (
        <div className="glass-card p-3 mb-4">
          <h4 className="font-medium text-gray-900 text-glass mb-2"> Aktivn kontext z vektorov databze:</h4>
          <div className="space-y-2">
            {vectorContexts.map((ctx, index) => (
              <div key={index} className="glass-button p-2">
                <div className="flex items-center justify-between mb-1">
                  <span className="text-sm font-medium text-gray-900">{ctx.topic}</span>
                  <span className="text-xs text-blue-600">{Math.round(ctx.relevance * 100)}% relevance</span>
                </div>
                <p className="text-xs text-gray-600 text-glass">{ctx.snippet}</p>
                <p className="text-xs text-gray-500 text-glass mt-1">Zdroj: {ctx.source}</p>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Messages */}
      <div className="flex-1 space-y-4 overflow-y-auto p-4">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
          >
            <div className={`max-w-3xl ${message.type === 'user' ? 'order-2' : 'order-1'}`}>
              <div className={`glass-card p-4 ${
                message.type === 'user' 
                  ? 'bg-blue-100 ml-8' 
                  : message.type === 'ai'
                  ? 'bg-green-50 mr-8'
                  : 'bg-yellow-50 mx-8'
              }`}>
                <div className="flex items-start space-x-3">
                  <div className="text-lg">{getMessageIcon(message.type)}</div>
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <span className="font-medium text-gray-900 text-glass">
                        {message.type === 'user' 
                          ? user?.name 
                          : message.type === 'ai' 
                          ? 'Professor Kwark' 
                          : 'Systm'}
                      </span>
                      <span className="text-xs text-gray-500 text-glass">
                        {message.timestamp.toLocaleTimeString('cs-CZ')}
                      </span>
                      {message.confidence && (
                        <span className="text-xs text-blue-600">
                          {Math.round(message.confidence * 100)}% jistota
                        </span>
                      )}
                    </div>
                    
                    <div className="text-gray-700 text-glass whitespace-pre-line">
                      {message.content}
                    </div>
                    
                    {message.vectorContext && (
                      <div className="mt-2 pt-2 border-t border-gray-200">
                        <p className="text-xs text-gray-500 text-glass mb-1">
                           Pouit znalosti:
                        </p>
                        <div className="flex flex-wrap gap-1">
                          {message.vectorContext.map((context, index) => (
                            <span key={index} className="text-xs bg-blue-100 text-blue-700 px-2 py-1 rounded">
                              {context}
                            </span>
                          ))}
                        </div>
                      </div>
                    )}
                    
                    {message.relatedTopics && (
                      <div className="mt-2">
                        <p className="text-xs text-gray-500 text-glass mb-1">
                           Souvisejc tmata:
                        </p>
                        <div className="flex flex-wrap gap-1">
                          {message.relatedTopics.map((topic, index) => (
                            <button
                              key={index}
                              className="text-xs bg-gray-100 text-gray-700 px-2 py-1 rounded hover:bg-gray-200 transition-colors"
                              onClick={() => setInputMessage(`ekni mi vce o: ${topic}`)}
                            >
                              {topic}
                            </button>
                          ))}
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
        
        {isLoading && (
          <div className="flex justify-start">
            <div className="glass-card p-4 bg-green-50 mr-8">
              <div className="flex items-center space-x-3">
                <div className="text-lg"></div>
                <div>
                  <div className="flex items-center space-x-2 mb-1">
                    <span className="font-medium text-gray-900 text-glass">Professor Kwark</span>
                    <span className="text-xs text-gray-500 text-glass">pe...</span>
                  </div>
                  <div className="flex space-x-1">
                    <div className="w-2 h-2 bg-green-500 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-green-500 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-green-500 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
        
        <div ref={messagesEndRef} />
      </div>

      {/* Input Area */}
      <div className="glass-card p-4">
        <div className="flex space-x-3">
          <div className="flex-1">
            <textarea
              value={inputMessage}
              onChange={(e) => setInputMessage(e.target.value)}
              onKeyPress={handleKeyPress}
              placeholder={userRole === 'student' 
                ? "Zeptej se na cokoliv o fyzice, matematice nebo tvm pokroku..." 
                : "Zeptej se na analzu student, materily nebo doporuen..."}
              className="w-full glass-button px-4 py-3 border-0 focus:ring-2 focus:ring-blue-500 resize-none"
              rows={2}
              disabled={isLoading}
            />
            <div className="flex items-center justify-between mt-2">
              <div className="flex space-x-2">
                <button className="glass-button px-3 py-1 text-sm"> Piloit</button>
                <button className="glass-button px-3 py-1 text-sm"> Nahrt</button>
                {userRole === 'teacher' && (
                  <button className="glass-button px-3 py-1 text-sm"> Analza</button>
                )}
              </div>
              <span className="text-xs text-gray-500 text-glass">
                Enter pro odesln, Shift+Enter pro nov dek
              </span>
            </div>
          </div>
          
          <button
            onClick={sendMessage}
            disabled={!inputMessage.trim() || isLoading}
            className="glass-card-hover px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white font-medium hover:from-blue-600 hover:to-purple-700 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {isLoading ? '...' : ' Odeslat'}
          </button>
        </div>
      </div>
    </div>
  );
};