import React, { useState } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { ThemeToggle } from '../Theme/ThemeToggle';

interface StudentResponse {
  id: string;
  questionId: string;
  subject: string;
  topic: string;
  question: string;
  studentAnswer: string;
  correctAnswer: string;
  isCorrect: boolean;
  difficulty: 'easy' | 'medium' | 'hard';
  timestamp: Date;
  timeSpent: number; // seconds
  attempts: number;
  confidence: number; // 1-5 scale
}

interface PracticeRecommendation {
  id: string;
  type: 'weakness' | 'strength' | 'review';
  subject: string;
  topic: string;
  reason: string;
  description: string;
  estimatedTime: number;
  difficulty: 'easy' | 'medium' | 'hard';
  priority: 'low' | 'medium' | 'high';
}

interface StudentProfile {
  id: string;
  name: string;
  grade: number;
  avatar: string;
  totalResponses: number;
  correctResponses: number;
  weeklyActivity: number;
  strengths: string[];
  weaknesses: string[];
  preferredLearningTime: string;
  averageSessionTime: number;
}

export const ParentDashboard: React.FC = () => {
  const { user, logout } = useAuth();
  const [selectedChild] = useState<string>('child-1');
  const [activeTab, setActiveTab] = useState<'overview' | 'responses' | 'practice' | 'analytics'>('overview');
  const [responseFilter, setResponseFilter] = useState<'all' | 'correct' | 'incorrect'>('all');
  const [timeFilter, setTimeFilter] = useState<'week' | 'month' | 'all'>('week');

  const children: StudentProfile[] = [
    {
      id: 'child-1',
      name: 'Petra Svobodov',
      grade: 8,
      avatar: '',
      totalResponses: 234,
      correctResponses: 189,
      weeklyActivity: 4.5,
      strengths: ['Matematika', 'Logick mylen', 'Analytick lohy'],
      weaknesses: ['Chemie', 'Memorovn vzorc', 'Rychl vpoty'],
      preferredLearningTime: 'Odpoledne (14:00-16:00)',
      averageSessionTime: 25
    }
  ];

  const recentResponses: StudentResponse[] = [
    {
      id: 'r1',
      questionId: 'q1',
      subject: 'Fyzika',
      topic: 'Newtonovy zkony',
      question: 'Jak je vztah mezi silou, hmotnost a zrychlenm?',
      studentAnswer: 'F = m * a',
      correctAnswer: 'F = m * a (podle druhho Newtonova zkona)',
      isCorrect: true,
      difficulty: 'medium',
      timestamp: new Date('2025-01-29 14:30'),
      timeSpent: 45,
      attempts: 1,
      confidence: 4
    },
    {
      id: 'r2',
      questionId: 'q2',
      subject: 'Chemie',
      topic: 'Kyseliny a zsady',
      question: 'Jak je pH ist vody?',
      studentAnswer: '8',
      correctAnswer: '7 (neutrln)',
      isCorrect: false,
      difficulty: 'easy',
      timestamp: new Date('2025-01-29 15:15'),
      timeSpent: 120,
      attempts: 3,
      confidence: 2
    },
    {
      id: 'r3',
      questionId: 'q3',
      subject: 'Matematika',
      topic: 'Kvadratick rovnice',
      question: 'Vyete rovnici: x - 5x + 6 = 0',
      studentAnswer: 'x = 2, x = 3',
      correctAnswer: 'x = 2, x = 3',
      isCorrect: true,
      difficulty: 'hard',
      timestamp: new Date('2025-01-29 16:00'),
      timeSpent: 180,
      attempts: 2,
      confidence: 5
    },
    {
      id: 'r4',
      questionId: 'q4',
      subject: 'Biologie',
      topic: 'Bunn dlen',
      question: 'Kolik chromozom m lidsk buka?',
      studentAnswer: '48',
      correctAnswer: '46 (23 pr)',
      isCorrect: false,
      difficulty: 'easy',
      timestamp: new Date('2025-01-28 15:45'),
      timeSpent: 30,
      attempts: 1,
      confidence: 3
    }
  ];

  const practiceRecommendations: PracticeRecommendation[] = [
    {
      id: 'pr1',
      type: 'weakness',
      subject: 'Chemie',
      topic: 'Kyseliny a zsady',
      reason: 'Nzk spnost (40%) v poslednch 5 otzkch',
      description: 'Doporuujeme procviit zkladn vlastnosti kyselin a zsad, zejmna pH stupnici',
      estimatedTime: 30,
      difficulty: 'easy',
      priority: 'high'
    },
    {
      id: 'pr2',
      type: 'strength',
      subject: 'Matematika',
      topic: 'Pokroil rovnice',
      reason: 'Excelentn vkon (95%) v kvadratickch rovnicch',
      description: 'Petra m talent na matematiku! Meme pejt na kubick rovnice a funkce',
      estimatedTime: 45,
      difficulty: 'hard',
      priority: 'medium'
    },
    {
      id: 'pr3',
      type: 'review',
      subject: 'Fyzika',
      topic: 'Mechanika',
      reason: 'Dlouhodob absence (2 tdny) v tomto tmatu',
      description: 'Osvit znalosti mechaniky ped pechodem na termodynamiku',
      estimatedTime: 20,
      difficulty: 'medium',
      priority: 'medium'
    },
    {
      id: 'pr4',
      type: 'weakness',
      subject: 'Biologie',
      topic: 'Zklady buky',
      reason: 'ast chyby v zkladnch pojmech',
      description: 'Procviit strukturu buky a jej zkladn funkce',
      estimatedTime: 25,
      difficulty: 'easy',
      priority: 'high'
    }
  ];

  const selectedChildData = children.find(c => c.id === selectedChild) || children[0];

  const getFilteredResponses = () => {
    let filtered = recentResponses;
    
    if (responseFilter !== 'all') {
      filtered = filtered.filter(r => 
        responseFilter === 'correct' ? r.isCorrect : !r.isCorrect
      );
    }

    const now = new Date();
    if (timeFilter === 'week') {
      const weekAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
      filtered = filtered.filter(r => r.timestamp > weekAgo);
    } else if (timeFilter === 'month') {
      const monthAgo = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);
      filtered = filtered.filter(r => r.timestamp > monthAgo);
    }

    return filtered.sort((a, b) => b.timestamp.getTime() - a.timestamp.getTime());
  };

  const getResponseStats = () => {
    const responses = getFilteredResponses();
    const correct = responses.filter(r => r.isCorrect).length;
    const total = responses.length;
    const accuracy = total > 0 ? (correct / total) * 100 : 0;
    
    const avgTime = responses.length > 0 
      ? responses.reduce((sum, r) => sum + r.timeSpent, 0) / responses.length 
      : 0;
    
    const avgAttempts = responses.length > 0
      ? responses.reduce((sum, r) => sum + r.attempts, 0) / responses.length
      : 0;

    return { correct, total, accuracy, avgTime, avgAttempts };
  };

  const getSubjectPerformance = () => {
    const responses = getFilteredResponses();
    const subjects = new Map();
    
    responses.forEach(r => {
      if (!subjects.has(r.subject)) {
        subjects.set(r.subject, { correct: 0, total: 0 });
      }
      const subj = subjects.get(r.subject);
      subj.total++;
      if (r.isCorrect) subj.correct++;
    });

    return Array.from(subjects.entries()).map(([subject, data]) => ({
      subject,
      accuracy: data.total > 0 ? (data.correct / data.total) * 100 : 0,
      total: data.total
    })).sort((a, b) => b.accuracy - a.accuracy);
  };

  const getResponseColor = (isCorrect: boolean) => 
    isCorrect ? 'text-green-600 bg-green-100' : 'text-red-600 bg-red-100';

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'text-blue-600 bg-blue-100';
      case 'medium': return 'text-yellow-600 bg-yellow-100';
      case 'hard': return 'text-red-600 bg-red-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'text-red-600 bg-red-100';
      case 'medium': return 'text-yellow-600 bg-yellow-100';
      case 'low': return 'text-green-600 bg-green-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'weakness': return '';
      case 'strength': return '';
      case 'review': return '';
      default: return '';
    }
  };

  const stats = getResponseStats();
  const subjectPerformance = getSubjectPerformance();

  return (
    <div className="min-h-screen p-4">
      <div className="max-w-7xl mx-auto">
        <div className="glass-header p-6 mb-6 animate-slide-in-glass">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-glass">
                Rodiovsk pehled - {user?.name} 
              </h1>
              <p className="text-glass-light text-lg">
                Detailn sledovn pokroku a doporuen pro {selectedChildData.name}
              </p>
            </div>
            <div className="flex items-center space-x-4">
              <div className="glass-badge glass-badge-success px-4 py-2">
                <span className="font-bold"> {stats.accuracy.toFixed(1)}% spnost</span>
              </div>
              <ThemeToggle />
              <button onClick={logout} className="glass-button px-4 py-2 text-red-600 hover:text-red-700 font-medium">
                Odhlsit se
              </button>
            </div>
          </div>
        </div>

        {/* Child Selection & Navigation */}
        <div className="glass-card p-4 mb-6">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
            <div className="flex items-center space-x-4">
              <span className="text-4xl">{selectedChildData.avatar}</span>
              <div>
                <h3 className="font-semibold text-gray-900 text-glass">{selectedChildData.name}</h3>
                <p className="text-sm text-gray-600 text-glass">{selectedChildData.grade}. tda</p>
              </div>
            </div>
            
            <div className="flex space-x-2 flex-wrap">
              {[
                { id: 'overview', label: ' Pehled' },
                { id: 'responses', label: ' Odpovdi' },
                { id: 'practice', label: ' Doporuen' },
                { id: 'analytics', label: ' Analzy' }
              ].map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id as any)}
                  className={`px-6 py-3 transition-all font-medium text-lg ${
                    activeTab === tab.id 
                      ? 'glass-tab-active' 
                      : 'glass-tab'
                  }`}
                >
                  {tab.label}
                </button>
              ))}
            </div>
          </div>
        </div>

        {/* Overview Tab */}
        {activeTab === 'overview' && (
          <div className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div className="glass-card p-4 text-center">
                <div className="text-2xl mb-2"></div>
                <p className="font-semibold text-gray-900 text-glass">Celkem odpovd</p>
                <p className="text-xl font-bold text-blue-600">{selectedChildData.totalResponses}</p>
              </div>
              <div className="glass-card p-4 text-center">
                <div className="text-2xl mb-2"></div>
                <p className="font-semibold text-gray-900 text-glass">Sprvnch</p>
                <p className="text-xl font-bold text-green-600">{selectedChildData.correctResponses}</p>
              </div>
              <div className="glass-card p-4 text-center">
                <div className="text-2xl mb-2"></div>
                <p className="font-semibold text-gray-900 text-glass">spnost</p>
                <p className="text-xl font-bold text-purple-600">
                  {((selectedChildData.correctResponses / selectedChildData.totalResponses) * 100).toFixed(1)}%
                </p>
              </div>
              <div className="glass-card p-4 text-center">
                <div className="text-2xl mb-2"></div>
                <p className="font-semibold text-gray-900 text-glass">Tdenn aktivita</p>
                <p className="text-xl font-bold text-orange-600">{selectedChildData.weeklyActivity}h</p>
              </div>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <div className="glass-card p-6">
                <h3 className="text-lg font-bold text-gray-900 text-glass mb-4"> Siln strnky</h3>
                <div className="space-y-2">
                  {selectedChildData.strengths.map((strength, index) => (
                    <div key={index} className="glass-button p-3 flex items-center justify-between">
                      <span className="text-gray-900 text-glass">{strength}</span>
                      <span className="text-green-500"></span>
                    </div>
                  ))}
                </div>
              </div>

              <div className="glass-card p-6">
                <h3 className="text-lg font-bold text-gray-900 text-glass mb-4"> Oblasti ke zlepen</h3>
                <div className="space-y-2">
                  {selectedChildData.weaknesses.map((weakness, index) => (
                    <div key={index} className="glass-button p-3 flex items-center justify-between">
                      <span className="text-gray-900 text-glass">{weakness}</span>
                      <span className="text-yellow-500"></span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Responses Tab */}
        {activeTab === 'responses' && (
          <div className="space-y-6">
            <div className="glass-card p-4">
              <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
                <div className="flex space-x-4">
                  <select 
                    value={responseFilter} 
                    onChange={(e) => setResponseFilter(e.target.value as any)}
                    className="glass-button px-3 py-2"
                  >
                    <option value="all">Vechny odpovdi</option>
                    <option value="correct">Pouze sprvn</option>
                    <option value="incorrect">Pouze nesprvn</option>
                  </select>
                  <select 
                    value={timeFilter} 
                    onChange={(e) => setTimeFilter(e.target.value as any)}
                    className="glass-button px-3 py-2"
                  >
                    <option value="week">Posledn tden</option>
                    <option value="month">Posledn msc</option>
                    <option value="all">Ve</option>
                  </select>
                </div>
                
                <div className="grid grid-cols-3 gap-4 text-center">
                  <div>
                    <p className="text-sm text-gray-600 text-glass">Sprvnost</p>
                    <p className="font-bold text-green-600">{stats.accuracy.toFixed(1)}%</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 text-glass">Prm. as</p>
                    <p className="font-bold text-blue-600">{Math.round(stats.avgTime)}s</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 text-glass">Prm. pokusy</p>
                    <p className="font-bold text-purple-600">{stats.avgAttempts.toFixed(1)}</p>
                  </div>
                </div>
              </div>
            </div>

            <div className="space-y-4">
              {getFilteredResponses().map((response) => (
                <div key={response.id} className="glass-card p-6">
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex-1">
                      <div className="flex items-center space-x-3 mb-2">
                        <span className="font-semibold text-gray-900 text-glass">{response.subject}</span>
                        <span className="text-sm text-gray-600 text-glass"></span>
                        <span className="text-sm text-gray-600 text-glass">{response.topic}</span>
                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${getResponseColor(response.isCorrect)}`}>
                          {response.isCorrect ? ' Sprvn' : ' Nesprvn'}
                        </span>
                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${getDifficultyColor(response.difficulty)}`}>
                          {response.difficulty === 'easy' ? 'Snadn' : 
                           response.difficulty === 'medium' ? 'Stedn' : 'Tk'}
                        </span>
                      </div>
                      <p className="text-gray-900 text-glass font-medium mb-3">{response.question}</p>
                    </div>
                    <div className="text-right">
                      <p className="text-sm text-gray-600 text-glass">
                        {response.timestamp.toLocaleDateString('cs-CZ')} {response.timestamp.toLocaleTimeString('cs-CZ')}
                      </p>
                    </div>
                  </div>
                  
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="glass-button p-3">
                      <p className="text-sm text-gray-600 text-glass mb-1">Odpov studenta:</p>
                      <p className="text-gray-900 text-glass">{response.studentAnswer}</p>
                    </div>
                    <div className="glass-button p-3">
                      <p className="text-sm text-gray-600 text-glass mb-1">Sprvn odpov:</p>
                      <p className="text-gray-900 text-glass">{response.correctAnswer}</p>
                    </div>
                  </div>
                  
                  <div className="flex items-center justify-between mt-4 pt-4 border-t border-gray-200">
                    <div className="flex space-x-6 text-sm text-gray-600 text-glass">
                      <span> {response.timeSpent}s</span>
                      <span> {response.attempts} pokus()</span>
                      <span> Jistota: {response.confidence}/5</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Practice Tab */}
        {activeTab === 'practice' && (
          <div className="space-y-6">
            <div className="glass-card p-6">
              <h3 className="text-lg font-bold text-gray-900 text-glass mb-4"> Personalizovan doporuen</h3>
              <p className="text-gray-600 text-glass mb-4">
                Na zklad analzy odpovd {selectedChildData.name} doporuujeme nsledujc aktivity:
              </p>
              
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {practiceRecommendations.map((rec) => (
                  <div key={rec.id} className="glass-card p-4 h-full">
                    <div className="flex items-start justify-between mb-3">
                      <div className="flex items-center space-x-2">
                        <span className="text-2xl">{getTypeIcon(rec.type)}</span>
                        <span className={`px-2 py-1 rounded-full text-xs font-medium ${getPriorityColor(rec.priority)}`}>
                          {rec.priority === 'high' ? 'Vysok' : 
                           rec.priority === 'medium' ? 'Stedn' : 'Nzk'} priorita
                        </span>
                      </div>
                    </div>
                    
                    <h4 className="font-semibold text-gray-900 text-glass mb-2">
                      {rec.subject} - {rec.topic}
                    </h4>
                    
                    <p className="text-sm text-gray-600 text-glass mb-3">{rec.reason}</p>
                    <p className="text-sm text-gray-800 text-glass mb-4">{rec.description}</p>
                    
                    <div className="flex items-center justify-between text-xs text-gray-500 text-glass">
                      <span> {rec.estimatedTime} min</span>
                      <span className={`px-2 py-1 rounded ${getDifficultyColor(rec.difficulty)}`}>
                        {rec.difficulty === 'easy' ? 'Snadn' : 
                         rec.difficulty === 'medium' ? 'Stedn' : 'Nron'}
                      </span>
                    </div>
                    
                    <button 
                      onClick={() => alert(` Spoutm doporuenou aktivitu:\\n\\n${rec.subject} - ${rec.topic}\\n${rec.description}\\n\\nOdhadovan as: ${rec.estimatedTime} minut`)}
                      className="w-full mt-4 glass-card-hover py-2 bg-blue-500 text-white font-medium hover:bg-blue-600"
                    >
                       Zat procviovat
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        {/* Analytics Tab */}
        {activeTab === 'analytics' && (
          <div className="space-y-6">
            <div className="glass-card p-6">
              <h3 className="text-lg font-bold text-gray-900 text-glass mb-4"> Vkonnost podle pedmt</h3>
              <div className="space-y-4">
                {subjectPerformance.map((subject) => (
                  <div key={subject.subject} className="glass-button p-4">
                    <div className="flex items-center justify-between mb-2">
                      <span className="font-medium text-gray-900 text-glass">{subject.subject}</span>
                      <span className="text-sm text-gray-600 text-glass">
                        {subject.accuracy.toFixed(1)}% ({subject.total} odpovd)
                      </span>
                    </div>
                    <div className="w-full bg-gray-200 rounded-full h-3">
                      <div 
                        className="bg-gradient-to-r from-red-500 via-yellow-500 to-green-500 h-3 rounded-full transition-all duration-500"
                        style={{ width: `${subject.accuracy}%` }}
                      />
                    </div>
                  </div>
                ))}
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="glass-card p-6">
                <h4 className="font-semibold text-gray-900 text-glass mb-4"> Optimln as uen</h4>
                <div className="text-center">
                  <div className="text-3xl mb-2"></div>
                  <p className="text-lg font-medium text-gray-900 text-glass">{selectedChildData.preferredLearningTime}</p>
                  <p className="text-sm text-gray-600 text-glass">Nejlep vsledky dosahuje</p>
                </div>
              </div>

              <div className="glass-card p-6">
                <h4 className="font-semibold text-gray-900 text-glass mb-4"> Prmrn dlka studia</h4>
                <div className="text-center">
                  <div className="text-3xl mb-2"></div>
                  <p className="text-lg font-medium text-gray-900 text-glass">{selectedChildData.averageSessionTime} minut</p>
                  <p className="text-sm text-gray-600 text-glass">Na jednu session</p>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};